import React from 'react';
import { times } from 'lodash';
import { connect, batch } from 'react-redux';
import { ingCustomAdd } from '../ings-actions'
import './ings-custom.scss'

const stateToProps = state => {
    const { max, ingsCounts, left, ingsOrder } = { ...state.drinks.drinkCustom }

    const orders = ingsOrder.reduce((acc, ingId, idx) => {
        const ingPos = (acc[ingId] || [])
        ingPos.push(idx + 1)
        acc[ingId] = ingPos
        return acc;
    }, {})

    return {
        ingsCounts,
        left,
        orders,
        maxCapacity: max
    };
};

function dispatchToProps(dispatch) {
    return {
        addIng: (ing, n) => {
            batch(() => times(n, () => dispatch(ingCustomAdd(ing))))
        }
    };
}

function IngsCustom({ ing, left, addIng, ingsCounts, maxCapacity }) {

    const dotsCount = 4
    const ingCount = ingsCounts[ing.id] || 0
    const portionsCount = Math.ceil(maxCapacity / ing.portion)
    const levelsCount = Math.ceil(portionsCount / dotsCount) + 1
    const dotsGenCount = portionsCount < dotsCount ?
        portionsCount
        : dotsCount

    const dots = times(levelsCount, level => level)
        .flatMap(level => level ?
            times(dotsGenCount, () => ' ing-custom-selected' + level)
            : times(dotsGenCount, idx => ((idx + 1) * ing.portion) <= left ?
                ''
                : ' ing-custom-hidden'
            ).reverse()
        )
        .slice(ingCount, ingCount + dotsGenCount)
        .reverse()

    const canAdd = ing.portion <= left
    const ingCssClass = ing.usable && (canAdd || ingCount)
        ? ''
        : ' ing-custom-inactive'

    return (<div
        onClick={() => canAdd ? addIng(ing, 1) : false}
        className={'ing-custom ' + ingCssClass}>

        <p className='ing-custom'>{ing.name}</p>

        {dots.map((dotCssClass, idx) => {
            return (<span key={ing.id + idx}
                className={'ing-custom' + dotCssClass}>
                &nbsp;
            </span>)
        })}

    </div>)
}

export default connect(stateToProps, dispatchToProps)(IngsCustom);
